.universal-hero-section {
  min-height: 60vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: default;
}

.hero-background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.hero-background-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  filter: brightness(0.8) contrast(1.1);
  will-change: transform;
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
  z-index: -1;
}

.hero-background-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  filter: brightness(0.8) contrast(1.1);
  will-change: transform;
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
  z-index: -1;
  /* Дополнительные стили для видео */
  transition: opacity 0.8s ease-in-out;
  opacity: 1;
  /* Улучшенное воспроизведение */
  -webkit-backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-perspective: 1000;
  /* Полное отключение взаимодействия */
  pointer-events: none;
  cursor: default;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

/* Специальные классы для автовоспроизведения и зацикливания */
.hero-background-video[autoplay] {
  /* Принудительное автовоспроизведение */
  animation: video-autoplay 0.1s ease-in-out;
}

.hero-background-video[loop] {
  /* Принудительное зацикливание */
  animation: video-loop 0.1s ease-in-out;
}

/* Анимации для улучшения воспроизведения */
@keyframes video-autoplay {
  0% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@keyframes video-loop {
  0% {
    transform: translateZ(0) scale(1);
  }
  100% {
    transform: translateZ(0) scale(1.001);
  }
}

/* Дополнительные стили для надежного автовоспроизведения */
.hero-background-video[autoplay][loop] {
  /* Комбинированные стили для автовоспроизведения и зацикливания */
  -webkit-animation-play-state: running;
  animation-play-state: running;
  /* Принудительное обновление для лучшей производительности */
  -webkit-transform: translate3d(0, 0, 0) scale(1);
  transform: translate3d(0, 0, 0) scale(1);
  /* Улучшенная обработка для мобильных устройств */
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

.hero-fallback-image {
  z-index: -1;
  transition: opacity 0.8s ease-in-out;
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* Полное отключение взаимодействия */
  pointer-events: none;
  cursor: default;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.hero-default-background {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #CDA434 0%, #90ccec 100%);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 2;
}

.hero-content {
  position: relative;
  z-index: 3;
  text-align: center;
  color: white;
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
  line-height: 1.2;
}

.hero-subtitle {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 1rem;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
  opacity: 0.9;
}

.hero-description {
  font-size: 1.2rem;
  opacity: 0.95;
  margin-bottom: 1.5rem;
  line-height: 1.6;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

/* Дополнительные стили для улучшения автовоспроизведения */
.hero-background-video[autoplay][muted] {
  /* Специальные стили для видео с автовоспроизведением и без звука */
  -webkit-media-controls: none;
  -webkit-media-controls-panel: none;
  -webkit-media-controls-play-button: none;
  -webkit-media-controls-timeline: none;
  -webkit-media-controls-current-time-display: none;
  -webkit-media-controls-time-remaining-display: none;
  -webkit-media-controls-mute-button: none;
  -webkit-media-controls-volume-slider: none;
  -webkit-media-controls-fullscreen-button: none;
}

/* Улучшенная производительность для видео */
.hero-background-video {
  /* Принудительное использование GPU для рендеринга */
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  /* Оптимизация для видео */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  /* Улучшенная обработка пикселей */
  -webkit-perspective: 1000px;
  perspective: 1000px;
  /* Принудительное обновление слоя */
  will-change: transform, opacity;
}

/* Специальные стили для мобильных устройств */
@media (max-width: 768px) {
  .hero-background-video[autoplay] {
    /* Улучшенное автовоспроизведение на мобильных */
    -webkit-transform: translate3d(0, 0, 0) scale(1);
    transform: translate3d(0, 0, 0) scale(1);
  }
  
  .hero-background-video[loop] {
    /* Улучшенное зацикливание на мобильных */
    -webkit-animation-duration: 0.05s;
    animation-duration: 0.05s;
  }
}

/* Адаптивность */
@media (max-width: 768px) {
  .universal-hero-section {
    min-height: 50vh;
  }
  
  .hero-content {
    padding: 1rem;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.5rem;
  }
  
  .hero-description {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1.2rem;
  }
  
  .hero-description {
    font-size: 0.9rem;
  }
}

/* Дополнительные стили для надежного зацикливания */
.hero-background-video[loop] {
  /* Принудительное зацикливание */
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  /* Улучшенная обработка конца видео */
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

/* Специальные стили для разных браузеров */
@supports (-webkit-appearance: none) {
  /* WebKit браузеры (Safari, Chrome) */
  .hero-background-video[autoplay][loop] {
    -webkit-animation: video-autoplay-loop 0.1s ease-in-out infinite;
    animation: video-autoplay-loop 0.1s ease-in-out infinite;
  }
}

@supports not (-webkit-appearance: none) {
  /* Не-WebKit браузеры (Firefox) */
  .hero-background-video[autoplay][loop] {
    animation: video-autoplay-loop 0.1s ease-in-out infinite;
  }
}

/* Комбинированная анимация для автовоспроизведения и зацикливания */
@keyframes video-autoplay-loop {
  0% {
    opacity: 1;
    transform: translateZ(0) scale(1);
  }
  50% {
    opacity: 1;
    transform: translateZ(0) scale(1.001);
  }
  100% {
    opacity: 1;
    transform: translateZ(0) scale(1);
  }
}
